@include media-breakpoint-down(md) {
  .index-page {
    &__filter-section {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 0;
      display: flex;
      flex-direction: column;
      overflow-y: hidden;
      background-color: $color-white;
      z-index: 100;

      &--active {
        bottom: 0;
        height: auto;
        overflow-y: scroll; } } } }
